.about {
  margin: 30px auto;
  padding: 30px;
  max-width: 900px;
  background: #fff;
  border-radius: 10px;
  box-shadow: rgba(0, 0, 0, 0.1) 2px 4px 8px;
  display: flex;
  align-items: center;
  gap: 20px;
  flex-wrap: wrap;
}

/* Image Styling */
.about-img {
  flex: 1;
  min-width: 280px;
  max-width: 400px;
  text-align: center;
}

.about-img img {
  border-radius: 10px;
  width: 100%;
  height: auto;
  max-height: 350px;
  object-fit: cover;
}

/* Content Styling */
.about-content {
  flex: 2;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 10px;
}

.about-content h1 {
  color: #444;
  font-family: 'Poppins', sans-serif;
  font-size: 24px;
  border-bottom: 3px solid #777;
  display: inline-block;
  margin-bottom: 15px;
}

.about-content p {
  text-align: justify;
  font-size: 14px;
  line-height: 1.6;
  color: #555;
  padding: 0 10px;
}

/* ======== DARK MODE ========= */
#dark .about {
  background-color: #222;
  box-shadow: none;
}

#dark .about-content h1 {
  color: #b0a4a4;
  border-bottom: 3px solid #b0a4a4;
}

#dark .about-content p {
  color: #d0caca;
}

/* ======== Responsive Design ========= */
@media (max-width: 768px) {
  .about {
      flex-direction: column;
      text-align: center;
      padding: 20px;
  }

  .about-img img {
      max-height: 280px;
  }

  .about-content h1 {
      text-align: center;
  }

  .about-content p {
      padding: 10px;
  }
}
